  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #c91f1f;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 20px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

